import {React, useEffect, useState} from 'react';
import { useNavigate,useLocation } from 'react-router-dom';
import PageWrapper from 		'../../../../layout/PageWrapper/PageWrapper';
import Page from 				'../../../../layout/Page/Page';
import { cirurgicalFlow } from 	'../../../../menu';
import Header from '../../../../components/Header';
import  {DropdownItem, DropdownMenu, DropdownToggle} from '../../../../components/bootstrap/Dropdown';
import Dropdown from '../../../../components/bootstrap/Dropdown';
import Button from '../../../../components/bootstrap/Button';
import Card, {CardBody,CardHeader,CardTitle,CardFooter} from '../../../../components/bootstrap/Card';
import './style.css'
import Icon from '../../../../components/icon/Icon';
import Modal, { ModalHeader, ModalBody, ModalTitle, ModalFooter } from '../../../../components/bootstrap/Modal';



const Recebimento = () => {

	

	const location = useLocation();
   

	/*
	*	States
	*/

	const [NomePaciente, 		setNomePaciente] 			= useState(null)
	const [Procedimento, 		setProcedimento] 			= useState(null)
	const [Horario,      		setHorario] 		  		= useState(null)
	const [MedicoNome,      	setMedicoNome] 		  		= useState(null)
	// eslint-disable-next-line no-unused-vars
	const [OpcaoSelecionada, 	setOpcaoSelecionada] 		= useState(null)
	const [CodigoEtapa, 		setCodigoEtapa] 			= useState(null)
	const [CodigoAviso, 		setCodigoAviso] 			= useState(null)
	const [CodigoSalaCirurgia, 	setCodigoSalaCirurgia] 		= useState(null)
	const [CodigoProxEtapa, 	setCodigoProxEtapa] 		= useState(null)

	const [ModalConfirmDesfazerEtapa, 	setModalConfirmDesfazerEtapa] 		= useState(false)


	useEffect(() => {

		setNomePaciente		 (location.state.NOME_PACIENTE)
		setProcedimento		 (location.state.PROCEDIMENTO)
		setHorario			 (location.state.HORARIO)
		setMedicoNome		 (location.state.MEDICO)
		setCodigoEtapa		 (location.state.CD_ETAPA)
		setCodigoAviso		 (location.state.COD_AVISO)
		setCodigoSalaCirurgia(location.state.CD_SAL_CIR)
		setCodigoProxEtapa	 (location.state.PROX_ETAPA)
		
	
	}, [location]);


	const SelecionarOpcao = (opcao) => {
		setOpcaoSelecionada(opcao)
		/*
		console.log(OpcaoSelecionada)
		*/
		navigate('/portaria/confirmacao', {
			state: {
				NOME_PACIENTE: NomePaciente,
				MEDICO: MedicoNome,
				PROCEDIMENTO: Procedimento,
				HORARIO: Horario,
				OPCAO_SELECIONADA: 	opcao,
				CD_ETAPA: CodigoEtapa,
				COD_AVISO: CodigoAviso,
				CD_SAL_CIR: CodigoSalaCirurgia,
				PROX_ETAPA: CodigoProxEtapa
			}
		  });
		
	}; 


	const navigate = useNavigate()

	const alterarLocalidade = () => {
		localStorage.removeItem('CAPP-local');
		navigate('/')
	};




	return (
		<PageWrapper title={cirurgicalFlow.localChoice.text}>
			<Header
			titulo='Porta CC'
			localidade='Checklist'
		 >
				<Dropdown >
					<DropdownToggle>
						<Button
							color="light"
							icon="menu"
							isLight
						>
							<p> </p>
						</Button>
					</DropdownToggle>
					<DropdownMenu>
						<DropdownItem onClick={() => alterarLocalidade()}>
							Alterar Local
						</DropdownItem>
						<DropdownItem onClick={()=> setModalConfirmDesfazerEtapa(true)}> 
							Desfazer etapa do Paciente 
						</DropdownItem>
					</DropdownMenu>
				
					</Dropdown>
			</Header>
			
			<Page>
				<br />
				<div>
					<Card>
						<CardHeader className='header-card-paciente'>
							<CardTitle className='header-nome-paciente'>
								{NomePaciente}
							</CardTitle>
						</CardHeader>
						<CardBody>
							<div className='bloco-info-paciente-portacc'>
								<div>
									<Icon icon='person' size='8x' forceFamily='material' /><br />
									{MedicoNome}
								</div>
								<div style={{"width": "600px"}}>
									<Icon icon='health_and_safety' size='8x' forceFamily='material' /><br />
									{Procedimento}
								</div>
								<div>
									<Icon icon='schedule' size='8x' forceFamily='material' /><br />
									{Horario}
								</div>
							</div>
						</CardBody>

					</Card>
				</div>


				<div className='position-opcoes'>
					<h1>Checklist Prontuário</h1><br />
					<div style={{"maxWidth": "600px"}} className='opcoes'>
						<Card className='bloco-step-completo' onClick= {()=> SelecionarOpcao(1)} >
							<CardBody>
							<Icon
								icon='checklist_rtl' 
								color= '#fff' 
								size='9x' 
								forceFamily= 'material' 
							/>
							</CardBody>
							<CardFooter className='bloco-step-completo'>
								<h1>Completo</h1>
							</CardFooter>
						</Card>
						<div style={{"paddingLeft": "10px", "color": "transparent"}}>.</div>

						<Card className='bloco-parcial' onClick= {()=> SelecionarOpcao(2)} >
							<CardBody>
							<Icon
								icon='rule' 
								color= '#fff' 
								size='9x' 
								forceFamily= 'material' 
							/>
							</CardBody>
							<CardFooter className='bloco-parcial'>
								<h1>Parcial</h1>
							</CardFooter>
						</Card>
						<div style={{"paddingLeft": "10px", "color": "transparent"}}>.</div>
						
						<Card className='bloco-ausente' onClick= {()=> SelecionarOpcao(3)} >
							<CardBody>
							<Icon
								icon='cancel' 
								color= '#fff' 
								size='9x' 
								forceFamily= 'material' 
							/>
							</CardBody>
							<CardFooter className='bloco-ausente'>
								<h1>Ausente</h1>
							</CardFooter>
						</Card>

					</div>
					<div className='btn-back'>
					<Icon   onClick={() => window.history.go(-1)}
							icon='reply' 
							color= '#fff' 
							size='6x' 
							forceFamily= 'material' 
					/>
				</div>
				</div>
			

				<Modal isCentered isOpen={ModalConfirmDesfazerEtapa} isAnimation={false} is>
						<ModalHeader style={{"backgroundColor":"#FECE64"}}>
							<ModalTitle style={{"color": "#fff"}}> 
								ALERTA
							</ModalTitle>
						</ModalHeader>
						<ModalBody className='ModalBody'>
							<h3>Você realmente confirma desfazer etapa do Paciente?</h3><br />
						</ModalBody>
						<ModalFooter>
							<Button className='btn-style' size='lg' color='light'  onClick={()=> {setModalConfirmDesfazerEtapa(false)}}>
								FECHAR
							</Button>
							<Button className='btn-style' size='lg' color='success'  onClick={()=> { 
								navigate('/portaria/AssinaturaDesfazerEtapa', {
									state: {
										DADOS: location.state
									}
								});
							}	
							}>
								CONFIRMAR
							</Button>
						</ModalFooter>
				</Modal>



			</Page>
			
		</PageWrapper>
	);
};

export default Recebimento;
